import React from 'react';
import { Form } from 'react-bootstrap';
import { imageOnErrorHandler } from '../../../../../../utils/helpers/imageOnErrorHandler';
import InputErrorMessageProvider from '../../../../../../components/InputErrorMessageProvider/InputErrorMessageProvider';
import MyProgressBar, { FormState } from '../MyProgressBar/MyProgressBar';
import styles from '../../styles.module.scss';
import { Category } from '../../../../../../types/Category';

interface ICarCardPros {
  image: FileList,
  carName: string,
  category: string,
  categories: Category[],
  descriptionError: string,
  descriptionRegister: object,
  imageError: string,
  imageRegister: object,
  progressBarFields: FormState[],
}

const CarCard = (props: ICarCardPros): React.ReactElement => {
  const { image, carName, category, categories, imageError, imageRegister, descriptionError, descriptionRegister, progressBarFields } = props;
  return (
    <div className={styles['car-edit']}>
      <div className={styles['car-edit__card']}>
        <img
          src={image && image.length > 0 ? URL.createObjectURL(image[0]) : ''}
          alt="car-name"
          onError={imageOnErrorHandler}
        />
        <div className={styles['car-edit__details']}>
          <h2>
            {carName}
          </h2>
          <span>
            {category && category.length > 0 ? category : 'Категория не выбрана'}
          </span>
        </div>
        <div className={styles['car-edit__file-input-container']}>
          <InputErrorMessageProvider errorMessage={imageError}>
            <Form.Control
              type="file"
              accept="image/png, image/jpeg"
              {...imageRegister}
              isInvalid={!!(imageError)}
            />
          </InputErrorMessageProvider>
        </div>
        <MyProgressBar
          formStates={progressBarFields}
        />
        <Form.Group className={styles['car-edit__description-textarea']} controlId="carDescription">
          <Form.Label>Описание</Form.Label>
          <InputErrorMessageProvider errorMessage={descriptionError}>
            <Form.Control
              as="textarea"
              rows={4}
              aria-describedby="carDescription"
              placeholder="Введите описание"
              {...descriptionRegister}
              isInvalid={!!(descriptionError)}
            />
          </InputErrorMessageProvider>
        </Form.Group>
      </div>
    </div>
  );
};

export default CarCard;
